body {
    font-family: Arial, sans-serif;
  }
  
  .container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .product-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ccc;
    padding: 10px;
    margin: 10px 0;
  }
  
  .product-card span {
    font-size: 18px;
    font-weight: bold;
    color: blue;
  }
  
  .product-card .actions {
    display: flex;
    gap: 10px;
  }
  
  button {
    padding: 5px 10px;
    cursor: pointer;
  }