.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .add-button {
    font-size: 24px;
    font-weight: bold;
    padding: 5px 10px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    color: inherit;
  }
  
  .add-button:hover {
    color: blue;
  }
  
  .product-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ccc;
    padding: 10px;
    margin: 10px 0;
  }
  
  .product-card input {
    margin: 0 5px;
    padding: 8px;
    font-size: 16px;
  }
  
  .product-card span {
    font-size: 18px;
    font-weight: bold;
    color: blue;
  }
  
  .product-card .actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  button {
    padding: 5px 10px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #f0f0f0;
  }
  
  .columnaEd{
    display: flex;
    flex-direction: column;
  }